.notifications {
  position: fixed;
  z-index: 100;
  left: 50%;
  top: 0;
  width: 25%;
  min-width: 320px;
  transform: translateX(-50%); }
  .notifications__item {
    text-align: center;
    display: block;
    vertical-align: top;
    margin: 5px 0;
    background: #fff;
    border-radius: 5px;
    border: 1px solid #e04141;
    color: #e04141;
    padding: 5px 40px 5px 40px;
    position: relative; }
  .notifications__close {
    user-select: none;
    background: transparent;
    border: 0;
    outline: 0;
    text-align: center;
    cursor: pointer;
    font-family: "Montserrat", Helvetica, Arial, sans-serif;
    margin: 0;
    box-shadow: none;
    padding: 0;
    opacity: 1;
    border-radius: 0;
    text-transform: none;
    min-width: auto;
    max-width: none;
    transform: none;
    box-sizing: border-box;
    line-height: 1;
    padding: 2px 10px;
    font-size: 30px;
    color: #e04141;
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%); }
  .notifications__icon {
    display: inline-block;
    vertical-align: middle;
    margin: 0 20px 5px; }
  .notifications__text {
    display: inline-block;
    vertical-align: middle; }
